.Display {
  position: absolute;
  left: 50%;
  top: 50%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  width: 11em;
  height: 11em;
  padding-top: 2em;

  color: var(--session);
  font-weight: 700;
  text-align: center;

  transform: translate(-50%, -50%);
  transition: color 200ms ease-out;
  clip-path: circle(50%);
}

.Display--break {
  color: var(--break);
}

.Display__remaining {
  font-size: 1.875em;
}

.Display__reset {
  border: none;
  background-color: transparent;
  fill: hsla(228, 38%, 26%, 1);
}

.Display__reset svg {
  pointer-events: none;
}